import { graphql } from "../graphql"
import { getNode, getNodes, getNodesByType } from "./../datastore/index"
import express from "express"
import inspector from "inspector"
import { globalTracer } from "opentracing"

import reporter from "../reporter"
import { initTracer } from "../utils/tracer"
import { detectPortInUseAndPrompt } from "../utils/detect-port-in-use-and-prompt"
import {
  buildSchema,
  customizeSchema,
  initialize,
  sourceNodes,
  startGraphQLServer,
} from "."
import { store, saveState, savePartialStateToDisk } from "../redux"
import { IDataLayerContext } from "./types"
import { IDebugInfo, IProgram } from "../commands/types"
import { getDataStore } from "../datastore"
import { Listener } from "../utils/start-server"
import { printInstructions } from "../utils/print-instructions"
import {
  ContentEngineConfiguration,
  ContentEnginePublicAPI,
  SyncState,
  throwOutsideTestEnv,
} from ".."
import { ExecutionResult } from "graphql"
import withResolverContext from "../schema/context"
import { OperationContext } from "graphql-http"
import { ChildProcess } from "child_process"
import { frameworkHooks } from "../framework-hooks"

const tracer = globalTracer()

const openDebuggerPort = (debugInfo: IDebugInfo): void => {
  if (inspector.url() !== undefined) {
    return // fixes #26708
  }

  if (debugInfo.break) {
    inspector.open(debugInfo.port, undefined, true)
    // eslint-disable-next-line no-debugger
    debugger
  } else {
    inspector.open(debugInfo.port)
  }
}

// Return a user-supplied port otherwise the default Node.js debugging port
const getDebugPort = (port?: number): number => port ?? 9229

const getDebugInfo = (program: IProgram): IDebugInfo | null => {
  if (Object.prototype.hasOwnProperty.call(program, `inspect`)) {
    return {
      port: getDebugPort(program.inspect),
      break: false,
    }
  } else if (Object.prototype.hasOwnProperty.call(program, `inspectBrk`)) {
    return {
      port: getDebugPort(program.inspectBrk),
      break: true,
    }
  } else {
    return null
  }
}

export const contentEngine = (
  {
    verbose,
    openTracingConfigFile,
    host,
    port,
    directory,
    engineConfig,
    frameworkHooks: frameworkHooksPath,
  }: ContentEngineConfiguration = { directory: `` }
): ContentEnginePublicAPI => {
  if (frameworkHooksPath) {
    require(frameworkHooksPath)
  }

  frameworkHooks.startup.before()

  directory ||= process.cwd()

  // provide global Gatsby object
  global.__GATSBY = process.env.GATSBY_NODE_GLOBALS
    ? JSON.parse(process.env.GATSBY_NODE_GLOBALS)
    : {}

  const hostname = `0.0.0.0`

  const program = {
    directory,
    sitePackageJson: require(`${directory}/package.json`),
    port: (typeof port === `string` ? parseInt(port, 10) : port) || 8000,
    hostname,
    host: host || `localhost`,
    store,
    reporter,
    openTracingConfigFile: ``,
    debugInfo: null,
  } as IProgram

  program.debugInfo = getDebugInfo(program)

  console.info(
    `[content-engine] starting "${
      program.sitePackageJson.name || `unknown`
    }" engine`
  )

  program.verbose = verbose || false
  reporter.setVerbose(program.verbose)

  if (program.debugInfo) {
    openDebuggerPort(program.debugInfo)
  }

  if (openTracingConfigFile) {
    initTracer(openTracingConfigFile)
  }

  async function loadData(
    {
      webhookBody,
      parentSpan,
      shouldBuildSchema = true,
      connector,
    }: Partial<
      IDataLayerContext & {
        shouldBuildSchema?: boolean
        connector?: string
      }
    > = {
      shouldBuildSchema: true,
    }
  ): Promise<void> {
    const activity = reporter.activityTimer(`update integration data`)

    activity.start()

    try {
      await frameworkHooks.customizeSchema.before()
      await customizeSchema({
        parentSpan,
      })
      await frameworkHooks.customizeSchema.after()

      await frameworkHooks.sourceNodes.before()
      await sourceNodes({
        parentSpan,
        webhookBody,
        store,
        webhookSourcePluginName: connector,
      })
      await frameworkHooks.sourceNodes.after()

      await frameworkHooks.buildSchema.before()
      if (shouldBuildSchema) {
        await buildSchema({
          parentSpan,
        })
      }
      await frameworkHooks.buildSchema.after()

      saveState()
    } finally {
      activity.end()
    }
  }

  let app: express.Express
  let server: Listener
  let serverShouldBeRunning = false
  let initialized = false

  const publicInitialize = async ({ parentSpan }) => {
    await getDataStore().ready()
    const appExists = Boolean(app)

    if (!appExists && serverShouldBeRunning) {
      try {
        program.port = await detectPortInUseAndPrompt(program.port, hostname)
      } catch (e) {
        if (e.message === `USER_REJECTED`) {
          process.exit(0)
        }

        throw e
      }

      app = express()

      app.use((req, res, next) => {
        try {
          decodeURIComponent(req.path)
        } catch (e) {
          return res.status(500).send(`URI malformatted`)
        }

        return next()
      })
    }

    if (!initialized) {
      console.info(`[content-engine] initializing`)
      await initialize({
        parentSpan,
        program,
        reporter,
        app,
        engineConfig,
      })
      initialized = true
    }
  }

  frameworkHooks.startup.after()

  return {
    config: async () => {
      throw new Error(
        `contentEngine().config() can only be called when contentEngine({ runInSubProcess: true }) is set.`
      )
    },
    onStdOut: () => {
      throw new Error(
        `contentEngine().onStdOut() can only be called when contentEngine({ runInSubProcess: true }) is set.`
      )
    },
    onStdErr: () => {
      throw new Error(
        `contentEngine().onStdErr() can only be called when contentEngine({ runInSubProcess: true }) is set.`
      )
    },
    onMessage: () => {
      throw new Error(
        `contentEngine().onMessage() can only be called when contentEngine({ runInSubProcess: true }) is set.`
      )
    },
    sendMessage: () => {
      throw new Error(
        `contentEngine().sendMessage() can only be called when contentEngine({ runInSubProcess: true }) is set.`
      )
    },
    clearListeners: () => {
      throw new Error(
        `contentEngine().clearListeners() can only be called when contentEngine({ runInSubProcess: true }) is set.`
      )
    },
    initialize: async args => {
      const parentSpan = tracer.startSpan(`initialize`)
      await publicInitialize({ ...args, parentSpan })
      parentSpan.finish()
    },
    sync: async (
      { runServer, webhookBody, buildSchema = true, connector } = {
        buildSchema: true,
      }
    ): Promise<SyncState> => {
      console.log(`[content-engine] sync started`)
      await frameworkHooks.sync.before()
      if (typeof runServer !== `undefined`) {
        serverShouldBeRunning = runServer
      }

      const parentSpan = tracer.startSpan(`sync-data`)

      await publicInitialize({
        parentSpan,
      })

      try {
        await loadData({
          parentSpan,
          webhookBody,
          shouldBuildSchema: buildSchema,
          connector,
        })
      } catch (e) {
        console.error(`[content-engine] error loading data`, e)
        throw e
      }

      savePartialStateToDisk([`inferenceMetadata`])

      if (serverShouldBeRunning && !server) {
        server = await startGraphQLServer({
          parentSpan,
          program,
          app,
          store,
          reporter,
          loadData,
        })
      }

      if (server && !serverShouldBeRunning) {
        server.close()
      }

      await getDataStore().ready()
      parentSpan.finish()

      if (serverShouldBeRunning && server) {
        printInstructions(program)
      }

      await frameworkHooks.sync.after()

      return {}
    },
    stop: (): Promise<void> => {
      throw new Error(
        `contentEngine().stop() is only available when running in a subprocess with contentEngine({ runInSubProcess: true }) set.`
      )
    },
    restart: (): Promise<SyncState> => {
      throw new Error(
        `contentEngine().restart() is only available when running in a subprocess with contentEngine({ runInSubProcess: true }) set.`
      )
    },

    test: throwOutsideTestEnv({
      getNodes,
      getNodesByType,
      getNode,
      query: async (
        query: string,
        variables: Record<string, unknown>
      ): Promise<ExecutionResult> => {
        if (!initialized) {
          throw new Error(
            `contentEngine().query() called before first contentEngine().sync() completed.\nSchema customization has not run yet so the GraphQL API cannot be queried.`
          )
        }

        const { schema } = store.getState()

        const result = await graphql({
          schema,
          source: query,
          rootValue: {},
          contextValue: withResolverContext({
            schema: store.getState().schema,
            schemaComposer: store.getState().schemaCustomization.composer,
            context: {},
            customContext: store.getState().schemaCustomization.context,
          }) as unknown as OperationContext,
          variableValues: variables,
        })

        return result
      },
    }),

    getProcess(): ChildProcess {
      throw new Error(
        `contentEngine().process is only available when running in a subprocess with contentEngine({ runInSubProcess: true }) set.`
      )
    },
  }
}
